<template>
  <div class="hello">
    <h3 class="text-center">Cricket Player List</h3>
    <!-- <h1>{{ msg }}</h1> -->
      <section>
      <div class="search-wrapper">
        <input type="text" v-model="search" placeholder="Search title.."/>
        <label class="search_label">Search title: {{ search }}</label>
      </div>
      <div class="row" >
        <div class="columns large-4 medium-5" v-for="playerList in filteredList" :key="playerList.Id">
          <div class="card" >
          <!-- <img :src="`../assets/player-images/${playerList.Id}.jpg`"> -->
           <img :src="getImgUrl(playerList.Id)" v-bind:alt="playerList.Id" class="player_img">
          <!-- <img :src="'../assets/player-images/' + playerList.Id + '.jpg'" /> -->
          <div class="card-section">
            <!-- <p class='pName'> Player Name - {{ playerList.PFName }} ({{ playerList.TName}}) </p>
            <span class='pName'> Skill - {{ playerList.SkillDesc }}</span><br>
            <span class='pName'>  Players value - {{ playerList.Value }}</span>
            <div  v-if="playerList.UpComingMatchesList[0].MDate">
              <p class='pName'> Upcoming Matches  - {{ playerList.UpComingMatchesList[0].CCode }} Vs  {{ playerList.UpComingMatchesList[0].VsCCode }}</p>
              <p class='pName'> Match Time  - {{ format_date(playerList.UpComingMatchesList[0].MDate) }}</p>
            </div>
            <div v-else> 
              <p class='pName' style="color:red;"> <b>No Upcoming Matches</b></p>
            </div> -->
            <table>
              <tr>
                <td>Player Name</td>
                <td>{{ playerList.PFName }} ({{ playerList.TName }})</td>
              </tr>
              <tr>
                <td>Skill</td>
                <td>{{ playerList.SkillDesc }}</td>
              </tr>
              <tr>
                <td>Players value </td>
                <td>{{ playerList.Value }}</td>
              </tr>
              <tr v-if="playerList.UpComingMatchesList[0].MDate">
                <td>Upcoming match</td>
                <td>{{ playerList.UpComingMatchesList[0].CCode }} Vs  {{ playerList.UpComingMatchesList[0].VsCCode }}</td>
              </tr>
              <tr v-if="playerList.UpComingMatchesList[0].MDate">
                <td>Match Time</td>
                <td>{{ format_date(playerList.UpComingMatchesList[0].MDate) }}</td>
              </tr> 
              <tr v-if="!playerList.UpComingMatchesList[0].MDate" style="color:red;">
                <td>No Upcoming match</td>
                <td></td>
              </tr>              
            </table>
          </div>
        </div>
        </div>
      </div>
  </section>
</div>
</template>

<script>

import axios from 'axios'
import moment from 'moment'
export default {
  name: 'cricketPlayerList',
  props: {
    
  },
  data: function(){
    return{
      search: '',
      sort: 'views',
      msg: "List Of Players",
      playerLists: []
    }
  },
  mounted () {
    this.getPosts();
  },
  methods: {
      format_date(value){
         if (value) {
           return moment(String(value)).format('DD-MM-YYYY h:mm:ss a')
          }
      },
    getPosts() {
      let url ="https://api.jsonbin.io/b/604f1c137ea6546cf3ddf46e"
      axios.get(url).then((response) => {                
        console.log("response---",response.data.playerList)
        this.playerLists=response.data.playerList;
        console.log("this.playerLists----",this.playerLists.length)
      }).catch( error => { console.log(error); });
    },
      getImgUrl(pet) {
    var images = require.context('../assets/player-images/', false, /\.jpg$/)
    return images('./' + pet + ".jpg")
  }
},
computed: {
    filteredList() { 
      var players = this.playerLists.filter((playerList) => {
          return playerList.PFName.toLowerCase().includes(this.search.toLowerCase()) || playerList.TName.toLowerCase().includes(this.search.toLowerCase());
      });
      
      if (this.sort == 'views') {
        return players.sort(function(a, b) {
          return a.Value - b.Value
        });
      } else {
        return players;
      }
      
    }
  }
}
</script>

